import React, { useState, useEffect } from "react";

import { useDispatch } from "react-redux";

import {
  setCurrentTemplate,
  setDocumentStatus,
} from "../features/config/configSlice";
import { setJson } from "../features/json/jsonSlice";

function FormSelector(props) {
  const { projectId, templateId, documentId, token, userName } = props;

  const dispatch = useDispatch();

  const apiURL = "https://api-uat.extrack.com/";

  // PROJECT
  useEffect(() => {
    // console.log("-------> projectId has changed: ", projectId);
  }, [projectId]);
  // TEMPLATE
  useEffect(() => {
    // console.log("-------> templateId has changed: ", templateId);
    loadTemplate(templateId);
  }, [templateId]);
  // DOCUMENT
  useEffect(() => {
    // console.log("-------> documentId has changed: ", documentId);
  }, [documentId]);

  async function loadTemplate(pTemplateId) {
    // if (pTemplateId > 0)
    //   console.log("-------> load template id: ", pTemplateId);
    // setIsFetchingTemplates(true);

    let template;
    const templateHeaders = new Headers();
    templateHeaders.append("Authorization", `Bearer ${token}`);
    templateHeaders.append("Content-Type", "application/x-www-form-urlencoded");

    const requestOptions = {
      method: "GET",
      headers: templateHeaders,
      redirect: "follow",
    };

    // Check if project and template id have been set
    if (projectId > 0 && pTemplateId > 0) {
      await fetch(
        `${apiURL}V1.0/Project/${projectId}/Document/Form/Template/${pTemplateId}`,
        requestOptions
      )
        .then((response) => response.json())
        .then((result) => {
          // console.log(result);
          dispatch(setJson(JSON.parse(result.Config)));

          let newTemplate = { ...result };
          delete newTemplate.Config;
          dispatch(setCurrentTemplate(newTemplate));
          dispatch(setDocumentStatus("loaded"));
        })
        .catch((error) => console.log("error", error));
    }
  }

  return (
    <div>
      <ul>
        <li>project: {projectId}</li>
        <li>template: {templateId}</li>
        <li>document: {documentId}</li>
        <li>userName: {userName}</li>
        <li className="textcut textcut-lg">token: {token}</li>
      </ul>
    </div>
  );
}

export default FormSelector;
