import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";

import {
  selectDebugMode,
  setJson,
  selectJson,
  setRepeaterValue,
  selectRepeaterValue,
} from "../../features/config/configSlice";

import { getControlName } from "../../app/utilities";
import {
  DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  // Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument,
} from "./";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlusSquare, faTimes } from "@fortawesome/free-solid-svg-icons";

function Repeatable(props) {
  const json = props.controlJson;
  const { Type, Title, Id, Elements, IsVisible = true } = json;
  // console.log(`--->: Repeatable -> props: `, props)

  const dispatch = useDispatch();

  const repeaterValue = useSelector(selectRepeaterValue);
  const debugMode = useSelector(selectDebugMode);

  // console.log(`--->: Repeatable -> repeaterValue`, repeaterValue)

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument,
  };

  function updateJsonHandler(pJson, pRepeaterIndex) {
    // console.log('this repeater: ', pRepeaterIndex)
    // console.log('update this: ', pJson)

    const repeaterControlIndex = repeaterValue[pRepeaterIndex].findIndex(
      (item) => item.Id === pJson.Id
    );

    const newRepeaterValues = [...repeaterValue];
    console.log(
      `--->: updateJsonHandler -> newRepeaterValues`,
      newRepeaterValues
    );
    console.log(
      `--->: updateJsonHandler -> newRepeaterValues[pRepeaterIndex][repeaterControlIndex]`,
      newRepeaterValues[pRepeaterIndex][repeaterControlIndex]
    );

    newRepeaterValues[pRepeaterIndex] = [
      ...newRepeaterValues[pRepeaterIndex],
      pJson,
    ];

    // console.log('---> newRepeaterValues[pRepeaterIndex][repeaterControlIndex] ; ', newRepeaterValues[pRepeaterIndex][repeaterControlIndex])
    dispatch(setRepeaterValue(newRepeaterValues));

    // console.log('in this: ', json.Elements)
    // const mergedElements = [...json.Elements]

    // const elementIndex = mergedElements.findIndex(element => element.ID === pJson.ID)
    // mergedElements[elementIndex] = pJson
    // // console.log('--->elementIndex:  ', elementIndex)

    // props.updateJson({ ...json, Elements: mergedElements })
  }

  function addRepeatable() {
    dispatch(setRepeaterValue([...repeaterValue, Elements]));
  }
  function removeRepeatableByIndex(pIndex) {
    // console.log('---> removeRepeatableByIndex: ', pIndex)
    dispatch(
      setRepeaterValue([
        ...repeaterValue.filter((item, index) => index !== pIndex),
      ])
    );
  }

  // const children = Elements.map((control, index) => {
  //   const componentName = getControlName(control.Type)
  //   // console.log(`--->componentName: `, componentName)
  //   console.log('---> RP create item: ', control)

  //   const Component = components[componentName]
  //   return <Component key={index} controlJson={control} updateJson={updateJsonHandler} />
  //   // return <div key={index}>{control.Type}</div>
  // })
  const children = repeaterValue.map((value, repeaterIndex) => {
    // console.log(`--->value: `, value)

    const controls = Elements.map((control, index) => {
      // console.log('---> ', control)

      value.forEach((repeaterControl, index) => {
        if (control.Id === repeaterControl.Id)
          control = { control, ...repeaterControl };
      });

      const componentName = getControlName(control.Type);
      const Component = components[componentName];
      return (
        <Component
          key={index}
          controlJson={control}
          updateJson={(pJson) => updateJsonHandler(pJson, repeaterIndex)}
        />
      );
    });

    return (
      <div className="repeatable" key={`rep-${repeaterIndex}`}>
        <div className="repeatable-header">
          <h3 className="repeatable-title">
            [{repeaterIndex + 1}] {Title}
          </h3>
          <button
            className="btn btn-secondary btn-small"
            onClick={(e) => removeRepeatableByIndex(repeaterIndex)}
          >
            Remove
            <FontAwesomeIcon icon={faTimes} />
          </button>
        </div>
        {controls}
      </div>
    );
  });

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>

      <div className="control">
        {/* <div className="repeatable-header">
            <h3 className="repeatable-title">{Title}</h3>
            <button className="btn btn-secondary btn-small" onClick={e => console.log('ok')}>
              Remove
              <FontAwesomeIcon icon={faTimes} />
            </button>
          </div> */}
        {children}

        <button className="btn btn-primary" onClick={addRepeatable}>
          <FontAwesomeIcon icon={faPlusSquare} />
        </button>
      </div>
    </div>
  ) : (
    ""
  );
}

export default Repeatable;
