import React from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import {
  ListMulticontrol,
  Label,
  Text,
  Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
} from "../../components/controls/";

import { getControlName } from "../../app/utilities";

// import { selectDocumentStatus, setDocumentStatus } from '../../features/json/jsonSlice'
// import { useDispatch } from 'react-redux'

export default function Matrix(props) {
  const json = props.controlJson;
  // console.log(`-> json`, json)

  const { Type, Id, Title, Columns, Rows, Elements, IsVisible = true } = json;

  console.log(`--->: Matrix -> Elements`, Elements);

  const debugMode = useSelector(selectDebugMode);

  // const dispatch = useDispatch()
  // dispatch(setDocumentStatus('modified'))

  const components = {
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
  };

  let matrixLayout = [];
  for (let i = 0; i < Elements.length; i++) {
    // console.log('---> ROW', i)
    for (let j = 0; j < Elements[i].Elements.length; j++) {
      // console.log('---> COL', j)
      const element = Elements[i].Elements[j];
      // console.log(`--->: Matrix -> element`, element)
      const componentName = getControlName(element.Type);
      // console.log(`--->: Matrix -> componentName`, componentName)

      const Component = components[componentName];
      const controlComponent = (
        <Component
          key={`${componentName.substring(0, 4).toLowerCase()}-${
            element.ElementId
          }`}
          controlJson={element}
          updateJson={updateJsonHandler}
        />
      );

      matrixLayout.push(
        <div
          key={`${i}-${j}`}
          className="matrix-box"
          style={{
            backgroundColor: i % 2 > 0 ? "rgb(244, 244, 244)" : "",
            borderBottom: i % 2 > 0 ? "1px solid #CCC" : "",
          }}
        >
          {controlComponent}
        </div>
      );
    }
  }

  /**-------------------------------------------------**
   * @desc - JSON                                  -----*
   *----------------------------------------------------*/

  function updateJsonHandler(pJson) {
    console.log(`--->: updateJsonHandler -> pJson`, pJson);
    // console.log('updating... ', documentStatus)
    // if (documentStatus === 'loaded') dispatch(setDocumentStatus('modified'))

    // const controlPath = getControlPathById(pJson.Id)
    // // console.log(`--->: updateJsonHandler -> controlPath: `, controlPath)

    // let jsonToUpdate = JSON.parse(JSON.stringify(json))
    // // console.log(`--->: updateJsonHandler -> jsonToUpdate`, jsonToUpdate)
    // // ONLY FIST SUBLEVEL
    // jsonToUpdate.Sections[controlPath.section].Elements[controlPath.index] = pJson
    // dispatch(setJson(jsonToUpdate))
  }

  // let layout = Elements.map((item, index) => <div key={index}>boxCol</div>)

  return IsVisible ? (
    <div className="control-container control-container-matrix">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        <div className="control-row">
          <div
            style={{
              display: "grid",
              gridTemplateColumns: `repeat(${Columns}, 1fr)`,
              gridTemplateRows: `repeat(${Rows}, 1fr)`,
            }}
          >
            {matrixLayout}
          </div>
        </div>
      </div>
    </div>
  ) : (
    ""
  );
}
