import React, { useState } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import DateTimeInput from "../../components/DateTimeInput";

import { getCurrentDate, getDateObjFromDateString } from "../../app/utilities";

function DateTime(props) {
  const json = props.controlJson;
  const {
    Type,
    Title,
    Id,
    FormatDateTime = "yyyy-MM-dd hh:mm tt",
    IsBlank = false,
    IsVisible = true,
    DateTimeValue,
  } = json;

  const debugMode = useSelector(selectDebugMode);

  const defaultDateTime =
    DateTimeValue === "" || DateTimeValue === undefined
      ? `${
          getCurrentDate().toDateString() +
          " " +
          getCurrentDate().toLocaleTimeString()
        }`
      : DateTimeValue;

  const [dateTimeValue, setDateTimeValue] = useState(
    !IsBlank ? defaultDateTime : ""
  );
  // const dateTimeValue = (typeof DateTimeValue === 'string' && DateTimeValue !== '') ? getDateObjFromDateString(DateTimeValue) : DateTimeValue
  // console.log(`--->: DateTime -> dateTimeValue: `, dateTimeValue)

  function onDateTimeChangeHandler(pDateTime) {
    // console.log('---> onDateTimeChangeHandler: ', pDateTime)

    setDateTimeValue(pDateTime);

    const newJson = JSON.parse(JSON.stringify(json));
    if (pDateTime !== "") newJson.DateTimeValue = pDateTime;
    else delete newJson.DateTimeValue;
    newJson.IsBlank = pDateTime !== "" ? false : true;
    props.updateJson(newJson);
  }

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        <DateTimeInput
          format={FormatDateTime}
          dateTimeValue={dateTimeValue}
          onDateTimeChange={(value) => onDateTimeChangeHandler(value)}
        />
      </div>
    </div>
  ) : (
    ""
  );
}

export default DateTime;
