import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import FormSelector from "./components/FormSelector";
import Board from "./components/Board";
import Pagination from "./components/Pagination";

import {
  selectDebugMode,
  setDebugMode,
  selectCurrentToken,
  setCurrentToken,
  selectCurrentTemplate,
  selectCurrentDocument,
} from "./features/config/configSlice";

import "./App.scss";

function App() {
  const isDebugging = useSelector(selectDebugMode);
  const dispatch = useDispatch();

  const url = new URL(window.location.href);
  dispatch(setCurrentToken(url.searchParams.get("token")));

  const currentUserName = url.searchParams.get("user");
  const currentToken = useSelector(selectCurrentToken);

  const projectId = url.searchParams.get("projectId");
  const templateId = url.searchParams.get("templateId");
  const documentId = url.searchParams.get("documentId");
  const userName = currentUserName;

  const currentTemplate = useSelector(selectCurrentTemplate);
  const currentDocument = useSelector(selectCurrentDocument);

  return (
    <div className="App">
      <header className="App-header">
        <h1 title="v20.11.24">DYNAMIC FORM</h1>
        <h2>{currentTemplate.Title1 || selectCurrentDocument.Title}</h2>
        <div className="debug-toggle">
          <input
            type="checkbox"
            name="debug"
            checked={isDebugging}
            id="debug"
            onChange={() => dispatch(setDebugMode(!isDebugging))}
          />
          <label htmlFor="debug">Debug</label>
        </div>
      </header>
      <div className={`App-tools ${isDebugging ? "" : "hidden"}`}>
        <FormSelector
          projectId={projectId}
          templateId={templateId}
          documentId={documentId}
          userName={userName}
          token={currentToken}
        />
      </div>
      <Pagination />
      <Board />
    </div>
  );
}

export default App;
