import React from "react";
import { useSelector, useDispatch } from "react-redux";

import {
  selectOptionedControls,
  selectDebugMode,
} from "../../features/config/configSlice";

function ListMulticontrol(props) {
  const json = props.controlJson;

  const { Type, Title, Id, Data, IsVisible = true } = json;

  const debugMode = useSelector(selectDebugMode);
  const dispatch = useDispatch();

  function onChangeHandler(pEvent) {
    const selectedIndex = Number(pEvent.target.value);
    // console.log(`--->: onChangeHandler -> selectedIndex`, selectedIndex)
    if (selectedIndex >= 0) {
      const visibleControls =
        json.OptionedControls[selectedIndex].Controls !== undefined
          ? json.OptionedControls[selectedIndex].Controls
          : [];
      const hiddenControls = [];

      json.OptionedControls.forEach((option, index) => {
        // console.log('index: ', index, '  selectedIndex: ', selectedIndex)

        if (option.Controls !== undefined && index !== selectedIndex) {
          option.Controls.forEach((controlId) =>
            hiddenControls.push(controlId)
          );
          // console.log('index: ', index, '  selectedIndex: ', selectedIndex, '   hide: ', option.Controls)
        }
      });

      // console.log('---> hiddenControls: ', hiddenControls)
      // console.log('---> visibleControls: ', visibleControls)
      dispatch(selectOptionedControls({ hiddenControls, visibleControls }));
      // displayControls(visibleControls)
    }
  }

  function displayControls(pControlList) {
    // console.log('---> pControlList: ', pControlList)
    // dispatch(setControls(pControlList))
    // pControlList.forEach((item, index) => {
    //   console.log('---> ', item)
    // })
  }

  const options = Data.map((option, index) => (
    <option key={index} value={index}>
      {option}
    </option>
  ));

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <label htmlFor="listmulticontrol">{Title}</label>
        <select
          name="listmulticontrol"
          id="listmulticontrol"
          onChange={onChangeHandler}
        >
          <option value="-1">-----</option>
          {options}
        </select>
      </div>
    </div>
  ) : (
    ""
  );
}

export default ListMulticontrol;
