import React, { useState } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

export default function MatrixButton(props) {
  const json = props.controlJson;
  // console.log(`-> json`, json)

  const {
    Id,
    Title,
    Type,
    IsVisible = true,
    Selection,
    XAxisName,
    YAxisName,
    Legend,
    Columns,
    Rows,
    Elements,
  } = json;

  const debugMode = useSelector(selectDebugMode);

  const showLegend = Legend.ShowLegend;

  const [selectedButton, setSelectedButton] = useState([]);

  // const dispatch = useDispatch()
  // dispatch(setDocumentStatus('modified'))

  function buttonClickHandler(pButtonId) {
    const buttonPath = pButtonId.split("-");
    // console.log('---> ', buttonPath)
    // console.log('---> Selection is: ', Selection)

    const newJson = JSON.parse(JSON.stringify(json));

    if (Selection === "single") {
      for (let i = 0; i < newJson.Elements.length; i++) {
        for (let j = 0; j < newJson.Elements[i].Elements.length; j++) {
          if (newJson.Elements[i].Elements[j].Value)
            newJson.Elements[i].Elements[j].Value = false;
        }
      }
    }
    const buttonValue =
      Elements[buttonPath[0]].Elements[buttonPath[1]].Value !== undefined
        ? Elements[buttonPath[0]].Elements[buttonPath[1]].Value
        : false;

    newJson.Elements[buttonPath[0]].Elements[
      buttonPath[1]
    ].Value = !buttonValue;
    props.updateJson(newJson);
  }

  let matrixLayout = [];
  for (let i = 0; i < Elements.length; i++) {
    // console.log('---> ROW', i)
    for (let j = 0; j < Elements[i].Elements.length; j++) {
      // console.log('---> COL', j)
      const element = Elements[i].Elements[j];
      const id = `${i}-${j}`;
      const title = `${Legend.LegendValuesY.Name}: ${Legend.LegendValuesY.Values[i]}\n${Legend.LegendValuesX.Name}: ${Legend.LegendValuesX.Values[j]}`;

      matrixLayout.push(
        <div
          key={id}
          id={id}
          title={title}
          className="matrix-box matrix-button"
          style={{
            backgroundColor: element.Value
              ? element.BackgroundColorSelected
              : element.BackgroundColor,
          }}
          onClick={() => buttonClickHandler(id)}
        >
          {element.Title}
        </div>
      );
    }
  }

  const legendValuesX = Legend.LegendValuesX.Values.map((item, index) => (
    <li key={index}>{item}</li>
  ));
  const legendValuesY = Legend.LegendValuesY.Values.map((item, index) => (
    <li key={index}>{item}</li>
  ));

  const legend = showLegend ? (
    <div className="matrix-legend">
      <div>
        <h5>{Legend.LegendValuesY.Name}:</h5>
        {legendValuesY}
      </div>
      <div>
        <h5>{Legend.LegendValuesX.Name}:</h5>
        {legendValuesX}
      </div>
    </div>
  ) : (
    ""
  );

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        <div className="control-row">
          <div className="matrix-container">
            <h4 className="matrix-yaxis">{YAxisName}</h4>
            <div
              style={{
                display: "grid",
                gridTemplateColumns: `repeat(${Columns}, 1fr)`,
                gridTemplateRows: `repeat(${Rows}, 1fr)`,
              }}
            >
              {matrixLayout}
            </div>
            <h4 className="matrix-xaxis">{XAxisName}</h4>
          </div>
          <div className="matrix-footer">{legend}</div>
        </div>
      </div>
    </div>
  ) : (
    ""
  );
}
