import React from "react";

import { useSelector, useDispatch } from "react-redux";

import {
  selectDebugMode,
  setDocumentStatus,
} from "../../features/config/configSlice";

function ButtonSubmitRegister(props) {
  const json = props.controlJson;
  const { Type, Title, Id, IsVisible = true } = json;
  // console.log(`--->: Text -> IsVisible`, IsVisible)\

  const dispatch = useDispatch();
  const debugMode = useSelector(selectDebugMode);

  function onSubmitToRegisterHandler(pEvent) {
    // console.log('onSubmitToRegisterHandler: ', pEvent )
    dispatch(setDocumentStatus("tosend"));
  }

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>

      <div className="control">
        <button
          className="btn btn-secondary"
          onClick={onSubmitToRegisterHandler}
        >
          {Title}
        </button>
      </div>
    </div>
  ) : (
    ""
  );
}

export default ButtonSubmitRegister;
