import { createSlice } from "@reduxjs/toolkit";

export const configSlice = createSlice({
  name: "config",
  initialState: {
    debugMode: false,
    currentAction: "",
    currentTemplate: {},
    currentDocument: {},
    currentUserName: "",
    currentToken: "",
    currentSectionIndex: 0,
    documentStatus: "loading",
    currentAction: "",
    currentContact: { name: "", phoneNumber: "", companyName: "" },
    repeaterValue: [],
    optionedControls: {},
    openListId: 0,
  },
  reducers: {
    setDebugMode: (state, action) => {
      state.debugMode = action.payload;
    },
    setCurrentAction: (state, action) => {
      state.currentAction = action.payload;
    },
    setCurrentTemplate: (state, action) => {
      state.currentTemplate = action.payload;
    },
    setCurrentSectionIndex: (state, action) => {
      state.currentSectionIndex = action.payload;
    },
    callSection: (state, action) => {
      state.currentSectionIndex = action.payload;
    },
    prevSection: (state) => {
      state.currentSectionIndex -= 1;
    },
    nextSection: (state) => {
      state.currentSectionIndex += 1;
    },
    setDocumentStatus: (state, action) => {
      state.documentStatus = action.payload;
    },
    setCurrentAction: (state, action) => {
      state.currentAction = action.payload;
    },
    setCurrentContact: (state, action) => {
      state.currentContact = action.payload;
    },
    setCurrentDocument: (state, action) => {
      state.currentDocument = action.payload;
    },
    setRepeaterValue: (state, action) => {
      state.repeaterValue = action.payload;
    },
    setCurrentUserName: (state, action) => {
      state.currentUserName = action.payload;
    },
    setCurrentToken: (state, action) => {
      state.currentToken = action.payload;
    },
    setOptionedControls: (state, action) => {
      state.optionedControls = action.payload;
    },
    setOpenListId: (state, action) => {
      state.openListId = action.payload;
    },
  },
});

export const {
  setDebugMode,
  setCurrentToken,
  setCurrentUserName,
  setCurrentTemplate,
  setCurrentSectionIndex,
  callSection,
  prevSection,
  nextSection,
  setDocumentStatus,
  setCurrentAction,
  setCurrentContact,
  setRepeaterValue,
  setCurrentDocument,
  setOptionedControls,
  setOpenListId,
} = configSlice.actions;

export const selectDebugMode = (state) => state.config.debugMode;
export const selectCurrentSectionIndex = (state) =>
  state.config.currentSectionIndex;
export const selectCurrentTemplate = (state) => state.config.currentTemplate;
export const selectCurrentDocument = (state) => state.config.currentDocument;
export const selectCurrentUserName = (state) => state.config.currentUserName;
export const selectCurrentToken = (state) => state.config.currentToken;
export const selectCurrentContact = (state) => state.config.currentContact;
export const selectCurrentAction = (state) => state.config.currentAction;
export const selectDocumentStatus = (state) => state.config.documentStatus;
export const selectRepeaterValue = (state) => state.config.repeaterValue;
export const selectOptionedControls = (state) => state.config.value;
export const selectOpenListId = (state) => state.config.openListId;

export default configSlice.reducer;
