import React, { useState } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import TimeInput from "../../components/TimeInput";

function Time(props) {
  const json = props.controlJson;
  const {
    Type,
    Title,
    Id,
    FormatDateTime = "hh:mm tt",
    IsBlank = false,
    IsVisible = true,
    AddHour = 0,
    TimeValue,
  } = json;

  const debugMode = useSelector(selectDebugMode);

  const defaultTime =
    TimeValue === "" || TimeValue === undefined
      ? `${
          new Date().getHours() < 10
            ? "0" + new Date().getHours()
            : new Date().getHours()
        }:${
          new Date().getMinutes() < 10
            ? "0" + new Date().getMinutes()
            : new Date().getMinutes()
        }`
      : TimeValue;
  // console.log(`TimeValue: ${TimeValue} , defaultTime: ${defaultTime}`)

  const [timeValue, setTimeValue] = useState(!IsBlank ? defaultTime : "");

  function onTimeChangeHandler(pTime) {
    console.log(`--->: onTimeChangeHandler -> pTime`, pTime);
    setTimeValue(pTime);

    const newJson = JSON.parse(JSON.stringify(json));
    if (pTime !== "") newJson.TimeValue = pTime;
    else delete newJson.TimeValue;
    newJson.IsBlank = pTime !== "" ? false : true;
    props.updateJson(newJson);
  }

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        <TimeInput
          format={FormatDateTime}
          timeValue={timeValue}
          modifiers={{ AddHour }}
          onTimeChange={(value) => onTimeChangeHandler(value)}
        />
      </div>
    </div>
  ) : (
    ""
  );
}

export default Time;
