import React, { useState, useEffect, useRef } from "react";
import { useSelector } from "react-redux";

import * as htmlToImage from "html-to-image";
import { toPng, toJpeg, toBlob, toPixelData, toSvg } from "html-to-image";
import { format } from "date-fns";

import {
  selectDebugMode,
  selectCurrentUserName,
} from "../../features/config/configSlice";

function SignatureDate(props) {
  const json = props.controlJson;
  const { Type, Title, Id, IsVisible = true, Signature = "", DateValue } = json;
  const signatureDisplay = useRef(null);

  const debugMode = useSelector(selectDebugMode);
  const username = useSelector(selectCurrentUserName);

  const [signatureText, setSignatureText] = useState();
  const [selectedFont, setSelectedFont] = useState("");
  const [isSigning, setIsSigning] = useState(false);

  const fontList = [
    { label: "Style 01", name: "signature-font01" },
    { label: "Style 02", name: "signature-font02" },
    { label: "Style 03", name: "signature-font03" },
    { label: "Style 04", name: "signature-font04" },
  ];

  useEffect(() => {
    if (selectedFont.length > 0) generateImage();
  }, [selectedFont]);

  function generateImage() {
    htmlToImage
      .toPng(signatureDisplay.current)
      .then(function (dataUrl) {
        const rawData = dataUrl.replace("data:image/png;base64,", "");

        const newJson = JSON.parse(JSON.stringify(json));
        newJson.Signature = rawData;
        newJson.DateValue = format(new Date(), "yyyy-MM-dd");
        props.updateJson(newJson);
      })
      .catch(function (error) {
        console.error("--> Something went wrong!", error);
      });
  }

  function textChangeHandler(pEvent) {
    setSignatureText(pEvent.target.value);
  }
  function dropdownChangeHandler(pEvent) {
    setSelectedFont(pEvent.target.value);
  }
  function signClickHandler(pEvent) {
    setIsSigning(!isSigning);
  }

  const fontDropdownOptions = fontList.map((value, index) => (
    <option key={index} value={value.name}>
      {value.label}
    </option>
  ));

  const editSignature = isSigning ? (
    <div className="control-row control-inline">
      <input
        type="text"
        name="signatureText"
        id="signatureText"
        placeholder="Your Name"
        onChange={textChangeHandler}
      />
      <select
        name="fontDropdown"
        id="fontDropdown"
        onChange={dropdownChangeHandler}
      >
        <option value="">Choose a style</option>
        {fontDropdownOptions}
      </select>
    </div>
  ) : (
    ""
  );

  const btnSign = !isSigning ? (
    <div className="control-row">
      <button className="btn btn-primary btn-small" onClick={signClickHandler}>
        Sign
      </button>
    </div>
  ) : (
    ""
  );

  const signature = (
    <img src={`data:image/png;base64,${Signature}`} alt="" width="400px" />
  );

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          id=""
          cols="50"
          rows="6"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        {editSignature}
        <div className="control-row">
          {btnSign}
          <div className="signature-display">
            {signature}
            <div className="signature-timestamp">{DateValue}</div>
          </div>
        </div>
      </div>
      <div id="signaturePad"></div>
      <div className="signature-container">
        <div
          ref={signatureDisplay}
          className={`signature-preview ${selectedFont}`}
        >
          {signatureText}
          <div className="signature-user">{username}</div>
        </div>
      </div>
    </div>
  ) : (
    ""
  );
}
export default SignatureDate;
