import React, { useEffect } from "react";

import { useSelector, useDispatch } from "react-redux";

import {
  selectDebugMode,
  setDocumentStatus,
  setCurrentContact,
  selectCurrentContact,
  setCurrentAction,
  selectCurrentAction,
} from "../../features/config/configSlice";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faRedo, faTrash } from "@fortawesome/free-solid-svg-icons";

function SearchableSMSListView(props) {
  const json = props.controlJson;
  const { Type, Title, Title2, Id, Api, Value = [], IsVisible = true } = json;
  // console.log(`--->: Text -> json:`, json)

  const currentAction = useSelector(selectCurrentAction);
  const currentContact = useSelector(selectCurrentContact);
  const dispatch = useDispatch();

  const debugMode = useSelector(selectDebugMode);

  useEffect(() => {
    if (currentAction === "add-contact") {
      console.log("---> currentAction: ", currentAction);
      console.log("---> currentContact: ", currentContact);

      const registerValues = JSON.parse(Value);
      registerValues.push({
        Name: currentContact.name,
        Number: currentContact.phoneNumber,
        CompanyName: currentContact.companyName,
      });
      const newJson = JSON.parse(JSON.stringify(json));
      newJson.Value = JSON.stringify(registerValues);
      props.updateJson(newJson);

      dispatch(setCurrentAction("reset-currentContact"));
      dispatch(setCurrentContact(""));
    }
  }, [currentAction]);

  function deleteContact(pContactIndex) {
    const registerValues = JSON.parse(Value).filter(
      (item, index) => index !== pContactIndex
    );

    const newJson = JSON.parse(JSON.stringify(json));
    newJson.Value = JSON.stringify(registerValues);
    props.updateJson(newJson);
  }

  const contactList = Value.map((contact, index) => {
    return (
      <div key={index} className="listview-record">
        <div>{contact.Name}</div>
        <div>{contact.CompanyName}</div>
        <div>{contact.Number}</div>
        <button
          className="btn btn-secondary btn-small btn-icon"
          onClick={() => deleteContact(index)}
        >
          <FontAwesomeIcon icon={faTrash} />
          delete
        </button>
      </div>
    );
  });

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>

      <div className="control">
        {/* <h3>{Title}</h3> */}
        <div className="control-row">{contactList}</div>
      </div>
    </div>
  ) : (
    ""
  );
}

export default SearchableSMSListView;
