import React, { useState, useEffect } from "react";
import { useSelector, useDispatch } from "react-redux";

import { selectJson, setJson } from "../features/json/jsonSlice";
import {
  selectDocumentStatus,
  setDocumentStatus,
  selectCurrentSectionIndex,
  setCurrentSectionIndex,
} from "../features/config/configSlice";

import { getControlName, getControlPathById } from "../app/utilities";
import styles from "./Board.module.css";

import {
  DocumentTableOfContents,
  ListMulticontrol,
  Label,
  Text,
  Repeatable,
  ButtonSubmitPurchase,
  ButtonAddDocketNumber,
  API,
  Date,
  Image,
  ImageSingle,
  ButtonAddReportDate,
  QRScanner,
  QRScannerList,
  Signature,
  SignatureDateTime,
  SignatureDate,
  Matrix,
  Checkbox,
  Boolean,
  Trilean,
  TrileanLabel,
  ListSingle,
  ListMultiple,
  Time,
  DateTime,
  TimeLabel,
  DateLabel,
  ButtonAddSignature,
  RegisterSignatures,
  SearchableListEntryDialogSMS,
  ButtonAddSMS,
  SearchableSMSListView,
  SearchableListEntryDialogEmail,
  ButtonAddEmail,
  SearchableEmailListView,
  MatrixButton,
  DocumentSummaryGrid,
  ButtonSubmitRegister,
  ButtonCloseout,
  ButtonViewDocument,
  ButtonSendEmail,
  ButtonSendSMS,
} from "../components/controls/";

function Board() {
  const json = useSelector(selectJson);
  const currentSectionIndex = useSelector(selectCurrentSectionIndex);
  const dispatch = useDispatch();
  const documentStatus = useSelector(selectDocumentStatus);

  const [controls, setControls] = useState([]);

  const components = {
    DocumentTableOfContents,
    ListMulticontrol,
    Label,
    Text,
    Repeatable,
    ButtonSubmitPurchase,
    ButtonAddDocketNumber,
    API,
    Date,
    Image,
    ImageSingle,
    ButtonAddReportDate,
    QRScanner,
    QRScannerList,
    Signature,
    SignatureDateTime,
    SignatureDate,
    Matrix,
    Checkbox,
    Boolean,
    Trilean,
    TrileanLabel,
    ListSingle,
    ListMultiple,
    Time,
    DateTime,
    TimeLabel,
    DateLabel,
    ButtonAddSignature,
    RegisterSignatures,
    SearchableListEntryDialogSMS,
    ButtonAddSMS,
    SearchableSMSListView,
    SearchableListEntryDialogEmail,
    ButtonAddEmail,
    SearchableEmailListView,
    MatrixButton,
    DocumentSummaryGrid,
    ButtonSubmitRegister,
    ButtonCloseout,
    ButtonViewDocument,
    ButtonSendEmail,
    ButtonSendSMS,
  };

  const sectionsAmount = json.Sections !== undefined ? json.Sections.length : 0;
  const sectionJSON =
    json.Sections !== undefined ? json.Sections[currentSectionIndex] : [];
  const { Title, Elements } = sectionJSON;

  /* Watch for section changes * ---------------------------------------------------------------------------- */
  useEffect(() => {
    // if (Object.keys(json).length > 0)
    //   console.log("-------> json section updated: ", Elements);
    if (Elements !== undefined) {
      const controlsFromElements = Elements.map((element, index) => {
        const componentName = getControlName(element.Type);
        // console.log(`--->componentName: `, componentName);
        let controlComponent;
        if (components[componentName] !== undefined) {
          const Component = components[componentName];
          controlComponent = (
            <Component
              key={`${componentName.substring(0, 4).toLowerCase()}-${
                element.ElementId
              }`}
              controlJson={element}
              updateJson={updateJsonHandler}
            />
          );
        }
        return controlComponent;
      });
      setControls(controlsFromElements);
    } else setControls("");
  }, [Elements]);
  /* --------------------------------------------------------------------------- * Watch for section changes */

  /* Update the JSON * ------------------------------------------------------------------------------------- */
  function updateJsonHandler(pJson) {
    if (documentStatus === "loaded") dispatch(setDocumentStatus("modified"));

    const controlPath = getControlPathById(pJson.Id, currentSectionIndex, json);
    // LOG THE CURRENT UPDATED CONTROL
    // console.log(
    //   "%c " + pJson.Id + " ",
    //   "background:red;color:white;",
    //   controlPath,
    //   "updated:",
    //   pJson
    // );

    let jsonToUpdate = JSON.parse(JSON.stringify(json));
    // ONLY FIST SUBLEVEL
    jsonToUpdate.Sections[controlPath.section].Elements[
      controlPath.index
    ] = pJson;
    dispatch(setJson(jsonToUpdate));
  }
  /* ------------------------------------------------------------------------------------ * Update the JSON */

  const boardControls =
    documentStatus === "loading" ? (
      <div className="skeleton">
        {Array(14)
          .fill("")
          .map((item, index) => (
            <div
              key={index}
              className={`${
                index % 2 === 1 ? "skeleton-bloc-offset" : ""
              } skeleton-bloc`}
            ></div>
          ))}
      </div>
    ) : (
      controls
    );

  return (
    <div>
      <div className="currentSectionIndicator">
        {currentSectionIndex + 1} of {sectionsAmount}
      </div>
      <div className="board">{boardControls}</div>
    </div>
  );
}

export default Board;
