import camelcaseKeys from "camelcase-keys";

export function getControlName(pControlType) {
  return pControlType
    .split(".")
    .map((part) => part[0].toUpperCase() + part.slice(1))
    .join("");
}

export function getElementFromElementId(pJson, pElementID) {
  // console.log(`----------------> Looking for pElementID: `, pElementID, ' inside pJson: ', pJson)

  const currentJson = normalizeJson(pJson).Sections;
  // console.log(`--->: getElementFromElementId inside this: `, currentJson)
  let foundPath = [];

  if (pElementID !== "undefined") {
    currentJson.forEach((section, sectionIndex) => {
      section.Elements.forEach((item, index) => {
        if (item.hasOwnProperty("Elements")) {
          item.Elements.forEach((subitem, subindex) => {
            if (subitem.hasOwnProperty("Elements")) {
              subitem.Elements.forEach((subsubitem, subsubindex) => {
                if (
                  subsubitem.Id !== undefined &&
                  subsubitem.Id === pElementID
                ) {
                  // console.log('--> subsubpath: ', { sectionIndex, index, subindex, subsubindex })
                  foundPath.push([sectionIndex, index, subindex, subsubindex]);
                }
              });
            }

            if (subitem.Id !== undefined && subitem.Id === pElementID) {
              // console.log('--> subpath: ', { sectionIndex, index, subindex })
              foundPath.push([sectionIndex, index, subindex]);
            }
          });
        }

        if (item.Id !== undefined && item.Id === pElementID) {
          // console.log('--> path: ', { sectionIndex, index })
          foundPath.push([sectionIndex, index]);
        }
      });
    });
  }

  return foundPath;
}

// Fix conflict with React component 'Date' and Date JS Object
export function getCurrentDate(pDate = Date.now()) {
  // console.log(`--->: getCurrentDate -> pDate`, pDate)
  // return new Date().toLocaleDateString().split('/').reverse().join('-')
  // return new Date().toLocaleDateString()
  // return new Date()
  return new Date(pDate);
}
export function getDateObjFromDate(pDate) {
  const dateObj = new Date(pDate + " " + new Date().toTimeString());
  // console.log('utilities: ', pDate, dateObj)
  return dateObj;
}
export function getDateObjFromTime(pTime) {
  const dateObj = new Date(new Date().toDateString() + " " + pTime);
  // console.log('utilities: ', pDate, dateObj)
  return dateObj;
}
export function getDateObjFromDateString(pDate) {
  const dateObj = new Date(pDate);
  return dateObj;
}

export function normalizeJson(pJson) {
  // const json = JSON.parse(pJson)
  return camelcaseKeys(pJson, { deep: true, pascalCase: true });
}

//
const exportMetaData = [];
export function addToDocumentExport(pValue) {
  const controlIndex = exportMetaData.findIndex(
    (item) => item.Id === pValue.Id
  );
  // console.log(`--->: addToDocumentExport -> controlIndex`, controlIndex);
  if (controlIndex === -1) exportMetaData.push(pValue);
  else {
    const prevValue = { ...exportMetaData[controlIndex] };
    exportMetaData[controlIndex] = { ...prevValue, ...pValue };
  }
  // console.log(`--->: addToDocumentExport -> pValue`, pValue.Id)
  // console.log(`exportMetaData :`);
  console.table(exportMetaData);

  // exportMetaData[0].Elements.push(pValue)
  // console.log('---> addToDocumentExport: ', exportMetaData)
}

export function getDocumentExport() {
  return { Elements: exportMetaData };
}

export function getControlPathById(pControlId, pActiveSectionId, pJson) {
  // console.log(`--->: getControlPathById -> pControlId`, pControlId)
  const currentSection = pJson.Sections[pActiveSectionId];
  // let foundItem = {};
  let itemPath = {};

  currentSection.Elements.forEach((item, index) => {
    if (item.hasOwnProperty("Elements")) {
      item.Elements.forEach((subitem, subindex) => {
        if (subitem.hasOwnProperty("Elements")) {
          subitem.Elements.forEach((subsubitem, subsubindex) => {
            if (subsubitem.Id === pControlId) {
              // console.log('---> Trouv subsubitem: ', subsubitem)
              itemPath = {
                section: pActiveSectionId,
                index,
                subindex,
                subsubindex,
              };
              // foundItem = subsubitem;
            }
          });
        }

        if (subitem.Id === pControlId) {
          // console.log('---> Trouv subitem: ', subitem)
          itemPath = { section: pActiveSectionId, index, subindex };
          // foundItem = subitem;
        }
      });
    }

    if (item.Id === pControlId) {
      // console.log('---> Trouv item: ', item)
      itemPath = { section: pActiveSectionId, index };
      // foundItem = pJson.Sections[pActiveSectionId].Elements[index]
    }
  });
  // console.log('-- foundItem -> ', foundItem)
  return itemPath;
}
