import React, { useState, useEffect } from "react";

import { useSelector, useDispatch } from "react-redux";

import SelectSearchable from "./../SelectSearchable";

import {
  selectDebugMode,
  selectCurrentToken,
  setCurrentContact,
  setCurrentAction,
} from "../../features/config/configSlice";

function SearchableListEntryDialogSMS(props) {
  const json = props.controlJson;
  const { Type, Title, Id, IsVisible = true } = json;

  const debugMode = useSelector(selectDebugMode);
  const dispatch = useDispatch();

  const [contactList, setContactList] = useState([]);
  const [contactDropdownList, setContactDropdownList] = useState([]);
  const [selectedContact, setSelectedContact] = useState([]);

  const environment = "https://api-uat.extrack.com/V1.0/";
  const projectID = 1810;

  const apiUrl = `${environment}Project/${projectID}/Directory/Contacts`;
  // const token = JSON.parse(localStorage.getItem("dynFormToken"));
  const token = useSelector(selectCurrentToken);

  const apiHeaders = new Headers();
  apiHeaders.append("Authorization", `Bearer ${token}`);
  apiHeaders.append("Content-Type", "application/x-www-form-urlencoded");

  const requestOptions = {
    method: "GET",
    headers: apiHeaders,
    redirect: "follow",
  };

  useEffect(() => {
    fetch(apiUrl, requestOptions)
      .then((response) => response.json())
      .then((result) => {
        setContactList(result);
        setContactDropdownList(
          result.map((item) => {
            return {
              Id: item.Id,
              label: `${item.Contact.NameFirst} ${item.Contact.NameLast} | ${item.Company.Name}`,
            };
          })
        );
      });
  }, []);

  const addContact = (pItem) => {
    const contactIndex = contactList.findIndex(
      (contact) => contact.Id === pItem[0].Id
    );
    const contactDetails = contactList[contactIndex];

    dispatch(
      setCurrentContact({
        name: `${contactDetails.Contact.NameFirst} ${contactDetails.Contact.NameLast}`,
        phoneNumber: contactDetails.Contact.Number,
        companyName: contactDetails.Company.Name,
      })
    );
    dispatch(setCurrentAction("add-contact"));
    setSelectedContact(pItem);
  };

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          id=""
          cols="50"
          rows="6"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>

        <SelectSearchable
          items={contactDropdownList}
          selectedItems={[]}
          allowMultiple={false}
          onSelectItem={(item) => addContact(item)}
        />
      </div>
    </div>
  ) : (
    ""
  );
}

export default SearchableListEntryDialogSMS;
