import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faAngleUp, faAngleDown } from "@fortawesome/free-solid-svg-icons";

import { addToDocumentExport } from "../../app/utilities";

function Boolean(props) {
  const json = props.controlJson;
  const {
    Type,
    Title,
    Id,
    ShowComment,
    Collapsible,
    IsCollapsed,
    HasUnderLine,
    EntryTitle,
    Details,
    IsVisible = true,
    Value = "",
    CommentValue = "",
  } = json;

  const [collapsed, setCollapsed] = useState(IsCollapsed);

  const debugMode = useSelector(selectDebugMode);

  function onChangeHandler(pEvent) {
    const booleanValue = pEvent.target.value;
    const valuesToExport = { Type, Id, Value: booleanValue };
    addToDocumentExport(valuesToExport);

    const newJson = JSON.parse(JSON.stringify(json));
    newJson.Value = booleanValue;
    props.updateJson(newJson);
  }

  function onCommentChangeHandler(pEvent) {
    console.log("pEvent.target.value: ", pEvent.target.value);

    const newJson = JSON.parse(JSON.stringify(json));
    newJson.CommentValue = pEvent.target.value;
    props.updateJson(newJson);
  }

  function onToggleClickHandler(pEvent) {
    setCollapsed(!collapsed);
  }

  const comment = ShowComment ? (
    <input
      type="text"
      placeholder={EntryTitle}
      value={CommentValue}
      onChange={onCommentChangeHandler}
    ></input>
  ) : (
    ""
  );

  const details = Collapsible ? (
    <div className="control-details-container">
      <div className="control-details-toggle" onClick={onToggleClickHandler}>
        <FontAwesomeIcon icon={collapsed ? faAngleDown : faAngleUp} />
      </div>
      <div
        id="controlDetails"
        className={`${collapsed ? "hidden" : ""} control-details-details`}
      >
        {Details}
      </div>
    </div>
  ) : (
    ""
  );

  const entryTitle = EntryTitle ? (
    <label htmlFor="checkbox">{EntryTitle}</label>
  ) : (
    ""
  );

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>

      <div className="control">
        {Title}
        <div className="control-inline">{details}</div>
        <div
          className={`control-inline ${
            HasUnderLine ? "control-underline" : ""
          }`}
        >
          <div className="control-checkbox">
            <label htmlFor="yes">Yes</label>
            <input
              type="radio"
              id="yes"
              name={Id}
              value="Y"
              checked={Value === "Y"}
              onChange={onChangeHandler}
            />
          </div>
          <div className="control-checkbox">
            <label htmlFor="no">No</label>
            <input
              type="radio"
              id="no"
              name={Id}
              value="N"
              checked={Value === "N"}
              onChange={onChangeHandler}
            />
          </div>
        </div>
        <div className="control-inline">{entryTitle}</div>
        <div className="control-inline">{comment}</div>
      </div>
    </div>
  ) : (
    ""
  );
}

export default Boolean;
