import React, { useState } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import { getCurrentDate, getDateObjFromDate } from "../../app/utilities";
import DateInput from "../DateInput";

function Date(props) {
  const json = props.controlJson;
  let {
    Type,
    Title,
    Id,
    FormatDateTime = "yyyy-MM-dd",
    IsBlank = false,
    IsVisible = true,
    AddDay = 0,
    AddMonth = 0,
    DateValue,
  } = json;

  const debugMode = useSelector(selectDebugMode);

  const defaultDate =
    DateValue === "" || DateValue === undefined
      ? `${getCurrentDate().toDateString()}`
      : DateValue;

  const [dateValue, setDateValue] = useState(!IsBlank ? defaultDate : "");

  function onDateChangeHandler(pDate) {
    setDateValue(pDate);

    const newJson = JSON.parse(JSON.stringify(json));
    if (pDate !== "") newJson.DateValue = pDate;
    else delete newJson.DateValue;
    newJson.IsBlank = pDate !== "" ? false : true;
    props.updateJson(newJson);
  }

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <h3>{Title}</h3>
        <DateInput
          format={FormatDateTime}
          dateValue={dateValue}
          modifiers={{ AddDay, AddMonth }}
          onDateChange={(value) => onDateChangeHandler(value)}
        />
      </div>
    </div>
  ) : (
    ""
  );
}

export default Date;
