import React, { useState, useEffect } from "react";
import { useSelector } from "react-redux";

import { selectDebugMode } from "../../features/config/configSlice";

import Figure from "../Figure";

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faImage } from "@fortawesome/free-solid-svg-icons";

function ImageSingle(props) {
  const json = props.controlJson;
  const { Type, Title, Id, Data, IsVisible = true, Value = "" } = json;
  // console.log(`--->: ImageSingle -> IsVisible`, IsVisible)

  const debugMode = useSelector(selectDebugMode);

  function onChangeHandler(pEvent) {
    // setValue(pEvent.target.value)
    let imageData;
    const fileReader = new FileReader();
    fileReader.onload = (e) => {
      imageData = e.target.result.replace("data:image/png;base64,", "");

      const newJson = JSON.parse(JSON.stringify(json));
      newJson.Value = imageData;
      props.updateJson(newJson);
    };
    fileReader.readAsDataURL(pEvent.target.files[0]);
  }

  function onCloseHandler(pEvent) {
    console.log("---> ", pEvent);

    const newJson = JSON.parse(JSON.stringify(json));
    delete newJson.Value;
    props.updateJson(newJson);
  }

  useEffect(() => {
    // console.log('new data in control')
  }, [json]);

  const figure =
    Value !== "" ? (
      <Figure
        source={`data:image/png; base64, ${Value}`}
        onClose={onCloseHandler}
      />
    ) : (
      ""
    );

  return IsVisible ? (
    <div className="control-container">
      <div className={`tag ${debugMode ? "" : "hidden"}`}>
        {Type}
        <br />
        <span className="control-id">{Id}</span>
        <textarea
          name=""
          cols="10"
          rows="5"
          value={JSON.stringify(json)}
          disabled
        ></textarea>
      </div>
      <div className="control">
        <div className="control-row">
          <h3 htmlFor="title">{Title}</h3>
          <label htmlFor="file" className="btn btn-primary btn-label">
            <FontAwesomeIcon icon={faImage} />
            Select an image
          </label>
          <input
            type="file"
            id="file"
            name="file"
            className="btn-file"
            onChange={onChangeHandler}
          />
        </div>
        <div className="control-row">{figure}</div>
      </div>
    </div>
  ) : (
    ""
  );
}

export default ImageSingle;
